}

.btn-sign-in{
  padding: 15px;
  width: 250px;
  border: 3px ridge lightsteelblue;
  font-size: 25px;
  font-family: -webkit-body;
  font-weight: bolder;
  border-radius: 40px;
  letter-spacing: 8px;
  box-shadow: 0 0 20px 3px;
}

.darkorange{
  color:darkorange;
  font-weight: bolder;
  -webkit-text-stroke: 0.5px white;
  font-size: 25px;
}

.logo{
  letter-spacing: 5px;
}

.noAccount{
  margin-top: 8px;
  font-weight: 700;
  cursor: pointer;
}

.noAccount:hover{
  color:white;
}

.alertOff{
  display: none;

}

.alertOn{
  display: block;
}

.cardImg{
  width: 100%;
  border-radius: 10px;
}

.unfollow{
color:white;
}

.card{
  float:left;
  margin: 30px;
  width:370px;
  height: 530px;
  overflow: auto;
  margin-left:70px;
  font-family: -webkit-body;
  border-top-left-radius: 110px;
  border-bottom-right-radius: 110px;
  box-shadow: 19px 20px 20px 8px rgba(0,0,0,0.5);
  -webkit-transition: border-top-left-radius 1.5s, border-bottom-right-radius 1.5s, border-bottom-left-radius 1.5s, border-top-right-radius 1.5s;
  transition: border-top-left-radius 1.5s, border-bottom-right-radius 1.5s, border-bottom-left-radius 1.5s, border-top-right-radius 1.5s;
}

.card:hover{
  border-top-left-radius: 0px;
  border-bottom-right-radius: 0px;
  border-bottom-left-radius: 110px;
  border-top-right-radius: 110px;
}

.description{
 height: 80px;
 overflow: auto;
 padding:5px;
}

::-moz-selection {
  background: yellow;
  color:red;
}

::selection {
  background: yellow;
  color:red;
}

/* width */
::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  box-shadow: inset 0px 0px 5px yellow;
  background: white;
  border-radius: 10px;
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  background: rgb(30, 135, 221); 
  border-radius: 10px;
  border: 1px solid white
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: rgb(63, 63, 255); 
}

.card-header{
  font-weight: bolder;
  font-size: 25px;
}

.Adminhead{
  width:100%;
  background-color: orange;
  font-weight: bolder;
}

.priceTag{
  color:rgb(255, 251, 0);
  font-weight: bolder;
  text-shadow: 0 0 20px rgb(255, 251, 0);
  -webkit-text-stroke-color: red;
  -webkit-text-stroke-width: 0.2px;
  -webkit-animation: priceGlow 1s infinite;
          animation: priceGlow 1s infinite;
}

@-webkit-keyframes priceGlow {
  from {text-shadow: 0 0 20px rgb(255, 251, 0);}
  to {text-shadow: 0 0 0px;}
}

@keyframes priceGlow {
  from {text-shadow: 0 0 20px rgb(255, 251, 0);}
  to {text-shadow: 0 0 0px;}
}

.exit{
  font-size: 30px;
}
.unpad{
  padding:0px;
}

.displayIfLogged{
  display: block;
}

.hideIfNotLogged{
  display: none;
}

.followBlink{
  -webkit-animation: followblink 1s infinite;
          animation: followblink 1s infinite;
}

@-webkit-keyframes followblink {
  50% {color:red;}
  100% {color:black;}
}

@keyframes followblink {
  50% {color:red;}
  100% {color:black;}
}

